$primaryColor: #4527a4;

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

// PDF Embed
.wp-block-peb-pdf-embed {
	.bdbPDFEmbed {
		text-align: center;
	}

	.pebFullWindow {
		position: fixed;
		width: 100vw;
		height: 100vh;
		top: 0;
		left: 0;
		z-index: 999999;
	}

	.pebSizedContainer {
		max-width: 100%;
		display: inline-block;
		border: 1px solid #dadada;
	}

	.pebInLine {
		max-width: 100%;
		display: inline-block;
		border: 1px solid #dadada;
	}
}